@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@400;700&family=Titan+One&display=swap);
body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #96D9AD;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

h1 {
  font-family: 'Titan One', cursive;
  font-size: 3rem;
  color: #F2ADA7;
  -webkit-text-stroke: .5px #068BBF;
}

main {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

table {
    width: 95vw;
    margin-bottom: 20px;
}

thead {
    font-family : 'Source Sans Pro', sans-serif;
    font-weight: 700;
}

tbody {
    font-family: 'Source Sans Pro', sans-serif;
    font-weight: 400;
}

th, td {
    outline: .1px solid #068BBF;
    height: 40px;
    font-size: 1.2rem;
    background-color: #DAEAB3;
    color: #49A6A6;
}

p {
    font-family: 'Source Sans Pro', sans-serif;
    font-weight: 700;
    color: #068BBF;
}

.active {
    background-color: #F2ADA7;
}
form {
    font-family: 'Source Sans Pro', sans-serif;
    font-weight: 700;
    width: 95vw;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 20px;
}

label {
    font-size: 1rem;
    margin-right: 20px;
    color: #068BBF;
}

select {
    font-size: 1rem;
    height: 40px;
    width: 130px;
    border: none;
    padding: 5px;
}

input {
    font-size: 1rem;
    height: 31px;
    width: 130px;
    border: none;
    padding: 5px;
}

button {
    font-size: 1rem;
    font-weight: 700;
    height: 40px;
    width: 130px;
    background-color: #068BBF;
    border: none;
    margin-left: 20px;
    color: #DAEAB3;
}

button:focus, input:focus, select:focus {
    outline: none;
}
